import React, { useEffect, useState } from "react";
import axios from "axios";
import { publicAPI } from "../../apis/user";
import qs from "qs";
import { useNavigate } from "react-router";
import styled from "styled-components";

import KeywordModal from "./KeywordModal";
import Dropdown from "./Dropdown";
import RadioInput from "./RadioInput";

import searchIconImg from "../../assets/main/searchIcon.svg";
import keywordImg from "../../assets/main/checkIcon.svg";
import hrImg from "../../assets/main/hr.svg";
import deleteImg from "../../assets/main/deleteIcon.svg";
import arrowDownImg from "../../assets/main/arrowDownIcon.svg";
import arrowUpImg from "../../assets/main/arrowUpIcon.svg";

export default function Search({ searchResult }) {
  //    
  const [searchKeyword, setSearchKeyword] = useState();
  const [keyword, setKeyword] = useState([]);
  const [participants, setParticipants] = useState();
  const [playTime, setPlayTime] = useState("10");
  const [place, setPlace] = useState([]);
  const [purpose, setPurpose] = useState([]);
  const [gender, setGender] = useState([]);
  const [age, setAge] = useState([]);

  //  
  const keywordOptions = [
    { id: 0, title: "", param: "COOPERATIVE" },
    { id: 1, title: "", param: "QUICKNESS" },
    { id: 2, title: "", param: "SENSIBLE" },
    { id: 3, title: "", param: "BRAIN" },
    { id: 4, title: "", param: "CREATIVE" },
    { id: 5, title: "", param: "ACTIVE" },
    { id: 6, title: "", param: "PSYCHOLOGICAL" },
    { id: 7, title: "", param: "LUCK" },
    { id: 8, title: "", param: "COMMON_SENSE" },
    { id: 9, title: "", param: "PREPARATION" },
  ];
  const playTimeOptions = [10, 20, 30, 40, 50, 60];
  const placeOptions = [
    { id: 0, value: "", param: "INDOOR" },
    { id: 1, value: "", param: "OUTDOOR" },
  ];
  const purposeOptions = [
    { id: 0, title: "", param: "WORKSHOP" },
    { id: 1, title: "", param: "SPORTS_DAY" },
    { id: 2, title: "MT", param: "MT" },
    { id: 3, title: "", parma: "GATHERING" },
    { id: 4, title: "", param: "RETREAT" },
  ];
  const genderOptions = [
    { id: 0, value: "", param: "FEMALE" },
    { id: 1, value: "", param: "MALE" },
  ];
  const ageOptions = [
    { id: 0, value: "10 ", param: "UNDER_TEENAGER" },
    { id: 1, value: "10", param: "TEENAGER" },
    { id: 2, value: "20", param: "TWENTIES" },
    { id: 3, value: "30", param: "THIRTIES" },
    { id: 4, value: "40", param: "FORTIES" },
    { id: 5, value: "50 ", param: "FIFTIES" },
  ];

  //   
  const [menu, setMenu] = useState(false);
  const openMenu = () => {
    setMenu(!menu);
  };

  const onRemove = (category, id) => {
    if (category === keywordOptions) {
      setKeyword(
        keyword.filter((el) => {
          return el !== id;
        })
      );
    } else if (category === purposeOptions) {
      setPurpose(
        purpose.filter((el) => {
          return el !== id;
        })
      );
    }
  };

  // 
  const [keywordModal, setKeywordModal] = useState(false);
  const [purposeModal, setPurposeModal] = useState(false);

  const renderKeyword = (label, selected) => {
    return selected.map((el) => (
      <>
        <SelectedKeyword key={el}>
          '{label[el].title}' 
          <img
            src={deleteImg}
            id={el}
            style={{ width: "15px", height: "15px" }}
            onClick={(e) => {
              e.stopPropagation();
              onRemove(label, el);
            }}
          />
        </SelectedKeyword>
      </>
    ));
  };

  //  
  const reset = () => {
    setSearchKeyword([]);
    setKeyword([]);
    setParticipants("");
    setPlayTime(10);
    setPlace([]);
    setPurpose([]);
    setGender([]);
    setAge([]);
  };

  //  
  publicAPI.defaults.paramsSerializer = (params) => {
    return qs.stringify(params, { arrayFormat: "repeat" });
  };
  const submit = async () => {
    const requestURL = `/api/recreations/search`;
    let keywordParam = keyword.map((el) => keywordOptions[el].param);
    let purposeParam = purpose.map((el) => purposeOptions[el].param);

    try {
      const response = await publicAPI.get(requestURL, {
        params: {
          keyword: keywordParam,
          participants: participants,
          playTime: playTime,
          place: place,
          purpose: purposeParam,
          gender: gender,
          age: age,
        },
      });
      localStorage.removeItem("searchResult");
      localStorage.setItem("searchResult", JSON.stringify(response));
      console.log(response);
    } catch (error) {
      console.log(error);
    }
    // if (window.location.href !== "/search/list") {
    //   window.location.href = "/search/list";
    // }
  };

  return (
    <SearchEngine>
      {/*   */}
      <SearchWordBox>
        <SearchWord
          placeholder=" MT    ?"
          onChange={(e) => setSearchKeyword(e.target.value)}
          value={searchKeyword}
        />
        <img
          src={searchIconImg}
          style={{ width: "42.399px", height: "42.399px" }}
        />
      </SearchWordBox>
      <SearchBox>
        <Filters menu={menu.toString()}>
          {/*  */}
          <Filter>
            <LabelName htmlFor="keyword"></LabelName>
            <KeywordBox id="keyword" onClick={() => setKeywordModal(true)}>
              <img src={keywordImg} style={{ width: "20px", height: "20px" }} />
              {keyword.length === 0 ? (
                "   !"
              ) : (
                <SelectedKeywords>
                  {renderKeyword(keywordOptions, keyword)}
                </SelectedKeywords>
              )}
            </KeywordBox>
          </Filter>

          {/*  */}
          <Filter>
            <LabelName htmlFor="participants"></LabelName>
            <Input
              placeholder="  ."
              type="number"
              onChange={(e) => setParticipants(e.target.value)}
              value={participants}
            ></Input>
          </Filter>

          {/*  */}
          <Filter>
            <LabelName htmlFor="playTime"> </LabelName>
            <Dropdown
              list={playTimeOptions}
              setOption={setPlayTime}
              selectedOption={playTime}
            />
          </Filter>

          {/*  */}
          <Filter>
            <LabelName htmlFor="place"></LabelName>
            <RadioInput
              content={placeOptions}
              setOption={setPlace}
              selectedOption={place}
            />
          </Filter>
        </Filters>
        <Filters>
          <More isopen={menu}>
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <img src={hrImg} />
            </div>

            {/*  */}
            <Filter>
              <LabelName htmlFor="purpose"></LabelName>
              <KeywordBox id="purpose" onClick={() => setPurposeModal(true)}>
                <img
                  src={keywordImg}
                  style={{ width: "20px", height: "20px" }}
                />
                {purpose.length === 0 ? (
                  "   !"
                ) : (
                  <SelectedKeywords>
                    {renderKeyword(purposeOptions, purpose)}
                  </SelectedKeywords>
                )}
              </KeywordBox>
            </Filter>

            {/*  */}
            <Filter>
              <LabelName htmlFor="gender"></LabelName>
              <RadioInput
                content={genderOptions}
                setOption={setGender}
                selectedOption={gender}
              />
            </Filter>

            {/*  */}
            <Filter last="true">
              <LabelName htmlFor="age"></LabelName>
              <RadioInput
                content={ageOptions}
                setOption={setAge}
                selectedOption={age}
              />
            </Filter>
          </More>
        </Filters>
        <Menu onClick={openMenu}>
          {menu ? (
            <>
               
              <img style={{ width: "24px", height: "24px" }} src={arrowUpImg} />
            </>
          ) : (
            <>
               
              <img
                style={{ width: "24px", height: "24px" }}
                src={arrowDownImg}
              />
            </>
          )}
        </Menu>
      </SearchBox>
      <SearchBtns>
        <ResetBtn onClick={reset}></ResetBtn>
        <SearchBtn onClick={submit}> </SearchBtn>
      </SearchBtns>
      {keywordModal ? (
        <KeywordModal
          category="keyword"
          content={keywordOptions}
          modalControl={setKeywordModal}
          keywordControl={setKeyword}
          selectedOption={keyword}
        />
      ) : null}
      {purposeModal ? (
        <KeywordModal
          category="purpose"
          content={purposeOptions}
          modalControl={setPurposeModal}
          keywordControl={setPurpose}
          selectedOption={purpose}
        />
      ) : null}
    </SearchEngine>
  );
}

// 
const SearchEngine = styled.div`
  width: 958px;
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

//   input
const SearchWordBox = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -89.998px;
  width: 515.413px;
  height: 68.898px;
  box-shadow: 0px 0px 20px 0px rgba(27, 29, 31, 0.15);
  border-radius: 50px;
  background: #fff;
`;

const SearchWord = styled.input`
  width: 386px;
  height: 50px;
  display: inline-flex;
  padding: 9px 17px;
  align-items: center;
  gap: 48px;
  background: #fff;
  color: var(--gray-scale-464-c-52, #464c52);
  font-size: 20px;
  transition: max-height 0.3s ease-out;
  border: 0;
  outline: none;
`;

//  
const SearchBox = styled.div`
  width: 957px;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  background: var(--main-d-9-d-9-d-9, #19297c);
  box-shadow: 0px 15px 20px 0px rgba(27, 29, 31, 0.2);
  margin-bottom: 14.73px;
`;

//   
const Filters = styled.div`
  width: 885px;
  margin: 26px 47px 0 26px;
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 22px;
`;

const Filter = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: ${(props) => (props.last ? "46px" : 0)};
  color: #fff;
  font-size: 20px;
  font-weight: 700;
`;

const LabelName = styled.label`
  width: 117px;
`;

const KeywordBox = styled.div`
  width: 769px;
  height: 44px;
  border-radius: 50px;
  background: #fff;
  color: var(--gray-scale-9-fa-4-a-9, #9fa4a9);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
  padding-left: 20.5px;
  box-sizing: border-box;
`;

const SelectedKeywords = styled.div`
  display: flex;
  flex-direction: row;
  gap: 10px;
`;

const SelectedKeyword = styled.div`
  display: inline-flex;
  padding: 2px 10px;
  flex-direction: row;
  gap: 12px;
  align-items: center;
  border-radius: 20px;
  color: var(--gray-scale-1-b-1-d-1-f, #1b1d1f);
  background: #d9d9d9;
`;

const Input = styled.input`
  width: 230px;
  height: 44px;
  border-radius: 50px;
  background: #fff;
  border: none;
  color: var(--gray-scale-464-c-52, #464c52);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  padding-left: 21px;
  &::-webkit-inner-spin-button,
  &::-webkit-out-spin-button {
    -webkit-appearance: none;

    -moz-appearance: none;

    appearance: none;
  }
`;

const More = styled.div`
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 22px;
  overflow: hidden;
  opacity: ${(props) => (props.isopen ? "1" : "0")};
  visibility: ${(props) => (props.isopen ? "visible" : "hidden")};
  max-height: ${({ isopen }) => (isopen ? "1000px" : "0")};
  transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s;
`;

const Menu = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 69px;
  border-radius: 0px 0px 20px 20px;
  background: var(--main-scale-b-1-beff, #b1beff);
  font-size: 20px;
  font-weight: 700;
  gap: 8px;
`;

// 
const SearchBtns = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: end;
  gap: 39px;
  position: absolute;
  bottom: -60px;
  right: 0;
`;
const ResetBtn = styled.button`
  display: flex;
  padding: 9px 24px;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  border: 1px solid var(--gray-scale-1-b-1-d-1-f, #1b1d1f);
  background: var(--gray-scale-f-7-f-8-f-9, #f7f8f9);
  color: var(--gray-scale-1-b-1-d-1-f, #1b1d1f);
  font-size: 20px;
  font-weight: 700;
`;
const SearchBtn = styled.button`
  display: flex;
  padding: 9px 24px;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  border: 1px solid var(--gray-scale-1-b-1-d-1-f, #1b1d1f);
  background: var(--gray-scale-1-b-1-d-1-f, #1b1d1f);
  color: #fff;
  font-size: 20px;
  font-weight: 700;
`;
