import React, { useState } from "react";
import styled from "styled-components";
import fix from "../../assets/flowwrite/fix_flow_write.png";
import deleteIcon from '../../assets/flowwrite/deleteIcon.png';
import DetailKeywordModal from "../flowwrite/DetailKeywordModal";

export default function WriteRecreationInfo({ num, onDelete}) {
    const [title, setTitle] = useState("");
    const [time, setTime] = useState(10);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedKeywords, setSelectedKeywords] = useState([]);

  const handleTitleChange = (e) => {
    //     title  
    setTitle(e.target.value);
  };

  const handleTimeChange = (e) => {
    //     time  
    setTime(e.target.value);
  };

  const handleDeleteClick = () => {
    // onDelete    InfoBox .
    onDelete(num);
  };

  const handleDetailSearchClick = () => {
    if (!isModalOpen) {
      setIsModalOpen(true);
    }
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const handleSelectDetailKeywords = (keywords) => {
    setSelectedKeywords(keywords);
    handleCloseModal();
    setIsModalOpen(true);
  };

  const handleDeleteKeyword = (index, event) => {
    // Prevent the click event from propagating to the parent container (PurposeSearch)
    event.stopPropagation();
  
    const updatedKeywords = [...selectedKeywords];
    updatedKeywords.splice(index, 1);
    setSelectedKeywords(updatedKeywords);
  };

  return (
    <div style={{ display: "flex", gap: "8px", alignItems: "end", marginBottom:'8px' }}>
      {isModalOpen && (
              <DetailKeywordModal
                onClose={handleCloseModal}
                onSelectDetailKeywords={handleSelectDetailKeywords}
                selectedKeywords={selectedKeywords}
              />
            )}
      <Line time={time}></Line>
      <InfoBox time={time}>
        {/*   */}
        <RecreationTitle>
          <Number>{num}</Number>
          <RecreationTitleInput
            type="text"
            value={title}
            onChange={handleTitleChange}
            placeholder="  "
            style={{ fontSize: "20px", fontStyle: "normal", fontWeight: "700", border: "none", outline: "none" }}
          />
          <img src={fix} alt="Fix" style={{ width: '24px', height: '24px', cursor: 'pointer' }} onClick={handleDeleteClick} />        
        </RecreationTitle>
        
        {/*   */}
        {/* <KeywordBox>
          <Keyword> 1</Keyword>
          <Keyword> 2</Keyword>
          <Keyword> 3</Keyword>
        </KeywordBox> */}

        <KeywordBox onClick={handleDetailSearchClick}>
          {selectedKeywords.length === 0 ? (
            <KeywordInput
              type="text"
              placeholder="  3  ."
              style={{ width: '90%', height: '18px', backgroundColor: '#E9EBED' }}
            />
          ) : (
            <div style={{ width: '90%', display: 'flex' }}>
              {selectedKeywords.map((keyword, index) => (
                <React.Fragment key={index}>
                  <StyledKeyword>
                    {keyword}
                    <img
                      src={deleteIcon}
                      alt="Delete"
                      style={{ width: '20px', height: '20px', marginLeft: '5px', cursor: 'pointer' }}
                      onClick={(event) => handleDeleteKeyword(index, event)}
                    />
                  </StyledKeyword>
                  {index !== selectedKeywords.length - 1 && ' '}
                </React.Fragment>
              ))}
            </div>
          )}
        </KeywordBox>

        {/*    */}
        <PlayTime>
          <div style={{ fontSize: "16px", fontStyle: "normal", fontWeight: "500", color: "#1B1D1F" }}></div>
          <div style={{ fontSize: "16px", fontStyle: "normal", fontWeight: "600", color: "#1B1D1F"}}>
          <PlayTimeInput
            type="text"
            value={time}
            onChange={handleTimeChange}
            style={{ fontSize: "16px", fontStyle: "normal", fontWeight: "600", color: "#9FA4A9", border: "none", outline: "none" }}
            />
        </div>
        </PlayTime>
      </InfoBox>
    </div>
  );
}

const Line = styled.div`
  width: 0px;
  height: ${(props) => `${Math.max((props.time / 10) * 119.004, 119.004)}px`};
  border: 5px solid #b1beff;
  border-radius: 20px;
  margin-right: 21px;
  {/*transition: height 0.5s cubic-bezier(0.4, 0, 0.2, 1);*/} // 
`;

const InfoBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  top: ${(props) => (props.time === 10 ? "0" : `calc(-${props.time / 10 - 1} * 119.04px)`)};
  min-height: 119.004px;
`;

const RecreationTitle = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 12px;
`;

const RecreationTitleInput = styled.input`
  &::placeholder {
    color: #9FA4A9;
  }

  &:focus::placeholder {
    color: transparent;
  }
`;

const PlayTimeInput = styled.input`
  width: 20px;
  &::placeholder {
    color: #9FA4A9;
  }

  &:focus::placeholder {
    color: transparent;
  }
`;

const Number = styled.div`
  width: 42px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #b1beff;
  border-radius: 50%;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  margin-right: 8px;
`;

const KeywordBox = styled.div`
  width: 371px;
  height: 37px;
  border-radius: 5px;
  background: #E9EBED;
  color: #9fa4a9;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
  margin-bottom: 15px;
  padding-left: 10px;
  box-sizing: border-box;
`;

const KeywordInput = styled.input`
  width: 90%;
  height: 18px;
  margin-left: 8px;
  border: none;
  outline: none;
  font-size: 16px;

  &::placeholder {
    color: #9FA4A9;
  }

  &:focus::placeholder {
    color: transparent;
  }
`;

const StyledKeyword = styled.span`
  display: flex;
  height: 25px;
  padding: 2px 10px;
  box-sizing: border-box;
  border-radius: 20px;
  background: #D9D9D9;
  font-size: 16px;
  color: #1B1D1F;
  margin-left: 8px;
  align-items: center;

  img {
    margin-left: 12px;
  }
`;

