import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Blank from "../assets/moreflow/blank.png";
import Share from "../assets/moreflow/share.png";
import Time from "../assets/moreflow/time.png";
import User from "../assets/moreflow/user.png";
import View from "../assets/moreflow/view.png";
import Write from "../assets/moreflow/write.png";
import Scrap from "../assets/moreflow/scrap.png";
import Scrap2 from "../assets/moreflow/scrap2.png";
import Close from "../assets/myflow/close.png";
import RecreationInfo from "../components/recreationInfo/RecreationInfo";
import { useLocation } from "react-router-dom";
import axios from "axios";

const JWT_TOKEN =
  "eyJhbGciOiJIUzI1NiJ9.eyJpZCI6MiwiaWF0IjoxNzA3Mjk1MzkzLCJleHAiOjE5MDcyOTg5OTN9.yEvU_V98IMhnC09lEL_BdxU7aQTx69BclrAd9zjZL64";

export default function MoreWatchFlow() {
  const [scrap, setScrap] = useState(false);
  //   
  const DoScrap = async (id) => {
    const response = await axios.post(
      `https://dev.avab.shop/api/flows/${id}/scraps`,
      {},
      {
        headers: {
          Accept: "*/*",
          Authorization: `Bearer ${JWT_TOKEN}`,
        },
      }
    );

    if (response.status === 200) {
      //    
      console.log(response.data);
      setScrap(true);
    } else {
      //    
      console.log(response.data);
    }
  };

  //     
  const [share, setShare] = useState(false);
  const [modal, setModal] = useState(false);
  //       
  const OpenModal = () => {
    setModal(true);
  };
  //       
  const ShareBtn = () => {
    setShare(true);
    navigator.clipboard
      .writeText(window.location.href)
      .then(() => {
        console.log("URL copied to clipboard");
      })
      .catch((err) => {
        console.error("Could not copy text: ", err);
      });
  };
  //        
  const close = () => {
    setModal(false);
    setShare(false);
  };

  // moreData 
  const [data, setData] = useState([]);
  const location = useLocation();
  const id = location.state.moreData.id;

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(`https://dev.avab.shop/api/flows/${id}`, {
          headers: { Authorization: `Bearer ${JWT_TOKEN}` },
        });
        setData(response.data.result);
      } catch (error) {
        console.error(error);
      }
    };
    fetchData();
    setScrap(false);
  }, [id, scrap]);

  useEffect(() => {
    console.log(data);
  }, [data]);

  return (
    data.length !== 0 && (
      <div style={{ backgroundColor: "#E9EBED" }}>
        {/*  */}
        {modal ? (
          <ModalContainer>
            <ModalBox>
              <CloseBtn onClick={close}>
                <img src={Close} alt="" />
              </CloseBtn>
              <ModalBoxDetail>
                <div>
                  <ModalTitle>
                     
                    <br />
                    !
                  </ModalTitle>
                </div>
                {share ? (
                  <AfterCopyBtn> </AfterCopyBtn>
                ) : (
                  <BeforeCopyBtn onClick={ShareBtn}> </BeforeCopyBtn>
                )}
              </ModalBoxDetail>
            </ModalBox>
          </ModalContainer>
        ) : (
          <></>
        )}

        <TitleContainer>
          <img src={Blank} alt="" style={{ width: "250px", height: "250px", marginTop: "86px" }} />
          <TitleBox>
            <DetailTitleBox>
              <KeyWord>{data.flowDetail.purposeList.map((p) => p).join(", ")}</KeyWord>
              <ScrapImg onClick={() => DoScrap(id)}>
                {data.flowDetail.isFavorite ? <img src={Scrap2} alt="" /> : <img src={Scrap} alt="" />}
              </ScrapImg>
            </DetailTitleBox>
            <FlowName>{data.flowDetail.title}</FlowName>

            {/*   - ,,, */}
            <FlowBoxDetailBox>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={Time} alt="" style={{ width: "38px", height: "38px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.flowDetail.totalPlayTime}</FlowBoxDetail>
              </FlowBoxDetails>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={View} alt="" style={{ width: "38px", height: "38px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.flowDetail.viewCount}</FlowBoxDetail>
              </FlowBoxDetails>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={Write} alt="" style={{ width: "35px", height: "35px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.flowDetail.author.username}</FlowBoxDetail>
              </FlowBoxDetails>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={User} alt="" style={{ width: "24px", height: "24px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.flowDetail.scrapCount}</FlowBoxDetail>
              </FlowBoxDetails>
            </FlowBoxDetailBox>

            {/*  */}
            <ShareImg onClick={OpenModal}>
              <img src={Share} alt="" />
            </ShareImg>
          </TitleBox>
        </TitleContainer>

        <FlowInfoContainer>
          <FlowInfoBox>
            <FlowInfoTitle>
              <div></div>
              <div></div>
            </FlowInfoTitle>

            <FlowInfoDetail>
              <div style={{ width: "284px" }}>
                <div style={{ display: "flex", marginBottom: "8px" }}>
                  <FlowInfo style={{ width: "28px" }}></FlowInfo>
                  <FlowInfo style={{ fontWeight: "400" }}>
                    {data.flowDetail.purposeList.map((p) => p).join(", ")}
                  </FlowInfo>
                </div>
                <div style={{ display: "flex" }}>
                  <FlowInfo> </FlowInfo>
                  <div>{data.flowDetail.totalPlayTime}</div>
                </div>
              </div>

              <Line />

              <div>
                <div style={{ display: "flex", marginBottom: "8px" }}>
                  <FlowInfo></FlowInfo>
                  <FlowInfo2>
                    {data.flowDetail.keywordList.map((keyword) => (
                      <div>{keyword}</div>
                    ))}
                  </FlowInfo2>
                </div>
                <div style={{ display: "flex", marginBottom: "8px" }}>
                  <FlowInfo></FlowInfo>
                  <FlowInfo2>
                    {data.flowDetail.gender.map((gender) => (
                      <div>{gender}</div>
                    ))}
                  </FlowInfo2>
                </div>
                <div style={{ display: "flex", marginBottom: "8px" }}>
                  <FlowInfo></FlowInfo>
                  <FlowInfo2>
                    {data.flowDetail.age.map((gender) => (
                      <div>{gender}</div>
                    ))}
                  </FlowInfo2>
                </div>
                <div style={{ display: "flex", marginBottom: "8px" }}>
                  <FlowInfo></FlowInfo>
                  <div>{data.flowDetail.participants}</div>
                </div>
              </div>
            </FlowInfoDetail>

            <FlowContainer>
              <FlowTitle> </FlowTitle>

              {/*   */}
              <RecreationBox>
                {data.recreations.map((recreation, i) => (
                  <RecreationInfo recreation={recreation} num={i} />
                ))}
              </RecreationBox>
            </FlowContainer>
          </FlowInfoBox>
        </FlowInfoContainer>
      </div>
    )
  );
}

// 
const ModalContainer = styled.div`
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  background: rgba(70, 76, 82, 0.5);
  z-index: 999;
`;

const ModalBox = styled.div`
  width: 440px;
  height: 435px;
  border-radius: 20px;
  background: white;
  position: fixed;
  top: 215px;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalBoxDetail = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
`;

const CloseBtn = styled.div`
  width: 42px;
  height: 42px;
  position: absolute;
  top: 20px;
  left: 378px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;

const ModalTitle = styled.div`
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  text-align: center;
  margin-bottom: 8px;
`;

const BeforeCopyBtn = styled.div`
  display: flex;
  box-sizing: border-box;
  width: 223px;
  padding: 15px 34px;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  background: #4036ed;
  color: white;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  text-align: center;
  cursor: pointer;
`;

const AfterCopyBtn = styled.div`
  display: flex;
  box-sizing: border-box;
  height: 54px;
  width: 223px;
  padding: 15px 34px;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  border: 1px solid #4036ed;
  background: white;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  color: #4036ed;
  text-align: center;
`;

const TitleContainer = styled.div`
  height: 403px;
  background-color: #8896df;
  display: flex;
  justify-content: center;
`;

const TitleBox = styled.div`
  width: 372px;
  height: 251px;
  gap: -24px;
  margin-top: 86px;
  margin-left: 251px;
  position: relative;
`;

const DetailTitleBox = styled.div`
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
`;

const KeyWord = styled.div`
  padding: 16px 34px;
  gap: 10px;
  border-radius: 50px;
  background: #a0ddff;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  text-align: center;
  color: #1b1d1f;
`;

const ScrapImg = styled.div`
  width: 42px;
  height: 42px;
  position: absolute;
  left: 330px;
  cursor: pointer;
`;

const FlowName = styled.div`
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  color: #1b1d1f;
  margin-top: 22px;
`;

const FlowBoxDetailBox = styled.div`
  margin-left: 279px;
  margin-top: -28px;
`;

const FlowBoxDetails = styled.div`
  width: 100%;
  height: 42px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const FlowBoxDetail = styled.div`
  width: 93px;
  height: 42px;
  font-size: 16px;
  display: flex;
  justify-content: right;
  align-items: center;
`;

const FlowBoxDetailImg = styled.div`
  width: 42px;
  height: 42px;
  display: flex;
  justify-content: right;
  align-items: center;
`;

const ShareImg = styled.div`
  width: 42px;
  height: 42px;
  margin-top: -55px;
  cursor: pointer;
`;

const FlowInfoContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
`;

const FlowInfoBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const FlowInfoTitle = styled.div`
  width: 608px;
  height: 83px;
  border-radius: 20px;
  border: 1px solid #cacdd2;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 13px;
  gap: 225px;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
`;

const FlowInfoDetail = styled.div`
  box-sizing: border-box;
  width: 608px;
  height: 158px;
  border-radius: 20px;
  border: 1px solid #cacdd2;
  background: white;
  margin-bottom: 40px;
  padding: 29px 20px;
  display: flex;
  align-items: center;
  flex: 1;
`;

const FlowInfo = styled.div`
  margin-right: 8px;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
`;

const FlowInfo2 = styled.div`
  display: flex;
  gap: 8px;
  max-width: 190px;
  flex-wrap: wrap;
`;

const FlowContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px 170px;
  margin-bottom: 131px;
  border-radius: 20px;
  border: 0.5px solid #9fa4a9;
  background: white;
`;

const Line = styled.div`
  border: 0.5px solid #cacdd2;
  width: 0.5px;
  height: 11vh;
  margin-right: 20px;
`;

const FlowTitle = styled.div`
  margin-bottom: 59px;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
`;

const RecreationBox = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-start;
  margin-bottom: 8px;
`;
