import styled from "styled-components";
import React, { useState } from "react";
import yellowStar from "../../assets/recreation/yellowStar.svg";
import greyStar from "../../assets/recreation/greyStar.svg";

export default function ReviewStars({ onStarClick }) {
  //   
  const [clicked, setClicked] = useState([false, false, false, false, false]);
  //  5    
  const array = [0, 1, 2, 3, 4];

  //   
  const starScore = (index) => {
    let star = [...clicked];
    for (let i = 0; i < 5; i++) {
      star[i] = i <= index ? true : false;
    }
    setClicked(star);
    onStarClick(star.filter((element) => element).length);
  };

  //    
  let clickedStarNum = clicked.filter((element) => true === element).length;

  return (
    <>
      <ReviewStarstainer>
        <StarsWrap>
          {array.map((index) => (
            <img
              key={index}
              onClick={() => starScore(index)}
              src={clicked[index] ? yellowStar : greyStar} // clicked  true yellowStar, false greyStar 
              alt="starIcon"
            />
          ))}
        </StarsWrap>
      </ReviewStarstainer>
      <StarNum>{clickedStarNum}/5</StarNum>
    </>
  );
}

const ReviewStarstainer = styled.div`
  display: flex;
  align-items: center;
  margin: 0px 20px;
`;

const StarsWrap = styled.div`
  width: 112px;
  display: flex;
  justify-content: space-between;
`;

const StarNum = styled.div`
  display: flex;
  align-items: center;
  color: #26282b;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
`;
