import { Route, Routes, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import Header from "./components/Header";
import Footer1 from "./components/Footer/Footer1";
import Footer2 from "./components/Footer/Footer2";
import Footer3 from "./components/Footer/Footer3";
import Main from "./pages/Main";
import MyPage from "./pages/Mypage";
import Search from "./pages/Search";
import SearchList from "./pages/SearchList";
import FlowWrite from "./pages/FlowWrite"; //   
import FlowWriteDetail from "./pages/FlowWriteDetail"; //    
import FlowWriteRecommend from "./pages/FlowWriteRecommend"; //    
import FlowWriteContent from "./pages/FlowWriteContent"; //    
import RecreationDetail from "./pages/recreation/RecreationDetail"; //   
import MyFlow from "./pages/MyFlow"; //    
import WatchFlow from "./pages/WatchFlow"; //  
import ScrapFlow from "./pages/ScrapFlow"; //    
import MoreMyflow from "./pages/MoreMyflow"; //    
import MoreWatchFlow from "./pages/MoreWatchFlow"; //     
import MoreScrapFlow from "./pages/MoreScrapFlow"; //    
import Login from "./components/Login";
import LoginLoading from "./pages/LoginLoading"; //    
import GlobalStyle from "./GlobalStyles"; //  
function App() {
  const navigate = useNavigate();
  const [selectedFooter, setSelectedFooter] = useState(<Footer1 />);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [loginModal, setLoginModal] = useState(false);

  const routes = (
    <Routes>
      {/*  */}
      <Route path={process.env.PUBLIC_URL + "/"} element={<Main />} />
      {/*   */}
      <Route path="/search" element={<Search />} />
      {/*   */}
      <Route path="/mypage" element={<MyPage handleLogin={setIsLoggedIn} />} />
      {/*    */}
      <Route path="/search/list" element={<SearchList />} />
      {/*     */}
      <Route path="/flow/write" element={<FlowWrite />} />
      {/*     */}
      <Route path="/flow/write/detail" element={<FlowWriteDetail />} />
      {/*     */}
      <Route path="/flow/write/recommend" element={<FlowWriteRecommend />} />
      {/*     */}
      <Route path="/flow/write/content" element={<FlowWriteContent />} />
      {/*   */}
      <Route path="/flow/watch" element={<WatchFlow />} />
      {/*     */}
      <Route path="/flow/my" element={<MyFlow />} />
      {/*     */}
      <Route path="/flow/scrap" element={<ScrapFlow />} />
      {/*     */}
      <Route path="/flow/moremyflow" element={<MoreMyflow />} />
      {/*      */}
      <Route path="/flow/morewatchflow" element={<MoreWatchFlow />} />
      {/*     */}
      <Route path="/flow/morescrapflow" element={<MoreScrapFlow />} />
      {/*    */}
      <Route
        path="/recreation/detail/:recreationId"
        element={<RecreationDetail />}
      />
      {/*    */}
      <Route
        path="/api/auth/login/kakao"
        element={<LoginLoading handleLogin={setIsLoggedIn} />}
      />
    </Routes>
  );

  //   
  useEffect(() => {
    if (window.localStorage.getItem("userId") !== null) {
      setIsLoggedIn(true);
    } else {
      setIsLoggedIn(false);
    }
  }, [isLoggedIn]);
  useEffect(() => {
    const currentPath = window.location.pathname;
    if (
      currentPath === "/flow/watch" ||
      currentPath === "/flow/my" ||
      currentPath === "/recreation/detail"
    ) {
      setSelectedFooter(<Footer2 />);
    } else if (currentPath === "/" || currentPath.startsWith("/search")) {
      setSelectedFooter(<Footer1 />);
    } else {
      setSelectedFooter(<Footer3 />);
    }
  }, [navigate]);

  return (
    <div className="App">
      <GlobalStyle />
      <Header
        isLoggedIn={isLoggedIn}
        handleLoginStatus={setIsLoggedIn}
        handleLoginModal={setLoginModal}
      />
      {loginModal ? (
        <Login
          handleLoginStatus={setIsLoggedIn}
          handleLoginModal={setLoginModal}
        />
      ) : null}
      {routes}
      {selectedFooter}
    </div>
  );
}

export default App;
