import React, { useEffect, useState } from "react";
import axios from "axios";
import styled from "styled-components";
import Scrap from "../../assets/watchflow/scrap.png";
import Scrap2 from "../../assets/watchflow/scrap2.png";
import Time from "../../assets/watchflow/time.png";
import View from "../../assets/watchflow/view.png";
import Write from "../../assets/watchflow/write.png";
import User from "../../assets/watchflow/user.png";
import Blank from "../../assets/watchflow/blank.png";
import { useNavigate } from "react-router-dom";

const JWT_TOKEN =
  "eyJhbGciOiJIUzI1NiJ9.eyJpZCI6MiwiaWF0IjoxNzA3Mjk1MzkzLCJleHAiOjE5MDcyOTg5OTN9.yEvU_V98IMhnC09lEL_BdxU7aQTx69BclrAd9zjZL64";

export default function FlowBox({ datas, setScrap }) {
  //   
  const DoScrap = async (id) => {
    const response = await axios.post(
      `https://dev.avab.shop/api/flows/${id}/scraps`,
      {},
      {
        headers: {
          Accept: "*/*",
          Authorization: `Bearer ${JWT_TOKEN}`,
        },
      }
    );

    if (response.status === 200) {
      //    
      console.log(response.data);
      setScrap(true);
    } else {
      //    
      console.log(response.data);
    }
  };

  //  
  const navigate = useNavigate();
  const moveToMoreWatchFlow = (moreData) => {
    navigate(`/flow/morewatchflow`, { state: { moreData } });
  };

  return (
    <div>
      <MyFlowBoxParent>
        {datas.map((data, i) => (
          <MyFlowBoxChild key={i}>
            {/*  */}
            <FlowBoxKeyWord>{data.purpose.map((kw) => kw).join(", ")}</FlowBoxKeyWord>

            {/*   */}
            <FlowBoxScrapBox>
              {data.isScraped ? (
                <FlowBoxScrapImg src={Scrap2} alt="O" onClick={() => DoScrap(data.id)} />
              ) : (
                <FlowBoxScrapImg src={Scrap} alt="X" onClick={() => DoScrap(data.id)} />
              )}
            </FlowBoxScrapBox>

            {/*   */}
            <FlowBoxTitle>{data.title}</FlowBoxTitle>

            {/*   */}
            <FlowBoxImg src={Blank} alt=" " />

            {/*   - ,,, */}
            <FlowBoxDetailBox>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={Time} alt="" style={{ width: "38px", height: "38px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.totalPlayTime}</FlowBoxDetail>
              </FlowBoxDetails>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={View} alt="" style={{ width: "38px", height: "38px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.viewCount}</FlowBoxDetail>
              </FlowBoxDetails>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={Write} alt="" style={{ width: "35px", height: "35px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.author.username}</FlowBoxDetail>
              </FlowBoxDetails>
              <FlowBoxDetails>
                <FlowBoxDetailImg>
                  <img src={User} alt=" " style={{ width: "24px", height: "24px" }} />
                </FlowBoxDetailImg>
                <FlowBoxDetail>{data.scrapCount}</FlowBoxDetail>
              </FlowBoxDetails>
            </FlowBoxDetailBox>

            {/*   */}
            <MoreBtn onClick={() => moveToMoreWatchFlow(data)}> </MoreBtn>
          </MyFlowBoxChild>
        ))}
      </MyFlowBoxParent>
    </div>
  );
}

const MyFlowBoxParent = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 370px);
  row-gap: 20px;
  column-gap: 120px;
`;

const MyFlowBoxChild = styled.div`
  width: 368px;
  height: 408px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.15);
  position: relative;
`;

const FlowBoxKeyWord = styled.div`
  background-color: #a0ddff;
  border-radius: 45px;
  position: absolute;
  color: #1b1d1f;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  padding: 16px 34px;
  margin-top: 37px;
  margin-left: 36px;
`;

const FlowBoxTitle = styled.div`
  margin-top: 116px;
  margin-left: 36px;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
`;

const FlowBoxScrapBox = styled.div`
  width: 42px;
  height: 42px;
  position: absolute;
  margin-top: 37px;
  margin-left: 291px;
`;

const FlowBoxScrapImg = styled.img`
  position: absolute;
  width: 22px;
  height: 30px;
  cursor: pointer;
`;

const FlowBoxImg = styled.img`
  margin-top: 23px;
  margin-left: 36px;
  width: 142px;
  height: 142px;
`;

const FlowBoxDetailBox = styled.div`
  margin-left: 239px;
  margin-top: -168px;
`;

const FlowBoxDetails = styled.div`
  width: 93px;
  height: 42px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const FlowBoxDetail = styled.div`
  width: 93px;
  height: 42px;
  font-size: 16px;
  display: flex;
  justify-content: right;
  align-items: center;
`;

const FlowBoxDetailImg = styled.div`
  width: 42px;
  height: 42px;
  display: flex;
  justify-content: right;
  align-items: center;
`;

const MoreBtn = styled.div`
  background-color: #b1beff;
  width: 370px;
  height: 76px;
  font-size: 20px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: absolute;
  top: 333px;
  border-radius: 0px 0px 20px 20px;

  &:hover {
    background-color: #a0ddff;
    transition: 0.2s;
  }
`;
