import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import write1 from '../assets/flowwrite/write_1.png';
import write2 from '../assets/flowwrite/write_2.png';
import write3 from '../assets/flowwrite/write_3.png';
import writeSelect4 from '../assets/flowwrite/write_select_4.png';
import line from '../assets/flowwrite/line.png';
import WithoutSaving from '../components/flowwrite/WithoutSavingModal.jsx'
// import TimeOut from '../components/flowwrite/TimeOutModal.jsx'
import NoTitle from '../components/flowwrite/NoTitleModal.jsx'
// import NoKeyword from '../components/flowwrite/NoKeywordModal.jsx'
import WriteRecreationInfo from "../components/flowwrite/WriteRecreationInfo.jsx";
import RecommendRecreation from "../components/flowwrite/RecommendRecreation.jsx";
import ScrapRecreation from "../components/flowwrite/ScrapRecreation.jsx";

export default function FlowWriteContent() {
  const navigate = useNavigate();
  const [titleModal, setTitleModal] = useState(null);
  const [saveModal, setSaveModal] = useState(null);
  const [flowTitle, setFlowTitle] = useState("");
  const [time] = useState(10);
  const [infoBoxes, setInfoBoxes] = useState([1]);
  const [numOfRecreationInfo, setNumOfRecreationInfo] = useState(1);
  const [recreationData, setRecreationData] = useState([]);
  const [selectedKeywords, setSelectedKeywords] = useState([]);
  const [playTime, setPlayTime] = useState('');
  
    useEffect(() => {
      //    localStorage playTime   .
      const savedPlayTime = localStorage.getItem('playTime');
      if (savedPlayTime) {
        setPlayTime(savedPlayTime);
      }
    }, []);

    useEffect(() => {
      const savedKeywords = localStorage.getItem('selectedKeywords');
      if (savedKeywords) {
        setSelectedKeywords(JSON.parse(savedKeywords));
      }
    }, []);

  const handleNextClick = () => {  
    if (flowTitle.trim() === "") {
      setTitleModal(<NoTitle onClose={() => setTitleModal(null)} />);
    } else {
      navigate('/flow/my');
    }
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const handleBeforeClick = () => {
    setSaveModal(<WithoutSaving onClose={() => setSaveModal(null)}  />);
  };
  
  const handleSaveClick = () => {
    handleNextClick();
  };

  useEffect(() => {
    // API  
    const fetchRecreationData = async () => {
      try {
        const response = await axios.get('https://dev.avab.shop/api/recreations/recommended', {
          params: {
            playTime: time,
            purpose: selectedKeywords.join(','),
            // keyword: 'ACTIVE',
            // participants: 20,
            // gender: 'FEMALE',
            // age: 'TWENTIES',
          }
        });
        // API     recreationData  
        setRecreationData(response.data.result.map(item => ({
          id: item.id,
          title: item.title,
          totalStars: item.totalStars,
          keywordList: item.keywordList,
          imageUrl: item.imageUrl,
          summary: item.summary,
          isFavorite: item.isFavorite
        })));
      } catch (error) {
        console.error('Error fetching recreation data:', error);
      }
    };

    // API   
    fetchRecreationData();
  }, [time, selectedKeywords]);

  const handleFlowTitleChange = (e) => {
    //     flowTitle  
    setFlowTitle(e.target.value);
  };

  const handleAddFlow = () => {
    //    
    setInfoBoxes(prevInfoBoxes => [...prevInfoBoxes, infoBoxes.length + 1]);
    setNumOfRecreationInfo(prevNum => prevNum);
  };

  const handleAddRecommendFlow = async () => {
    try {
      // API   
      const response = await axios.get('https://dev.avab.shop/api/recreations/recommended', {
          params: {
            playTime: time,
            purpose: 'WORKSHOP'
          }
        });
      //    
      const { title, keywordList, playTime } = response.data;
      console.log('  :', { title, keywordList, playTime });
      //   
      return { title, keywordList, playTime };
    } catch (error) {
      //     
      console.error('   :', error);
    }
  };

  const handleAddScrapFlow = async () => {
    try {
      // API   
      const response = await axios.get('https://dev.avab.shop/api/users/me/favorites/recreations')
      //    
      const { title, keywordList, playTime } = response.data;
      console.log('  :', { title, keywordList, playTime });
      //   
      return { title, keywordList, playTime };
    } catch (error) {
      //     
      console.error('   :', error);
    }
  };

  FlowWriteContent.handleAddRecommendFlow = handleAddRecommendFlow;
  FlowWriteContent.handleAddScrapFlow = handleAddScrapFlow;

  const handleDelete = (num) => {
    //   
    setInfoBoxes((prevInfoBoxes) =>
        prevInfoBoxes.filter((item) => item !== num)
    );
};

    return (
        <FlowWriteWrap>
          {saveModal && saveModal}
          {titleModal && titleModal}
          <ProgressbarStyle>
            <ProgressBarItem>
              <img src={write1} alt="Write 1" style={{ width: '50px', height: '50px' }} />
              <span></span>
              <img src={line} alt="line" style={{ width: '80px', height: '2px' }} />
            </ProgressBarItem>
            <ProgressBarItem>
              <img src={write2} alt="Write 2" style={{ width: '50px', height: '50px' }} />
              <span></span>
              <img src={line} alt="line" style={{ width: '80px', height: '2px' }} />
            </ProgressBarItem>
            <ProgressBarItem>
              <img src={write3} alt="Write 3" style={{ width: '50px', height: '50px' }} />
              <span> </span>
              <img src={line} alt="line" style={{ width: '80px', height: '2px' }} />
            </ProgressBarItem>
            <ProgressBarItem>
            <img src={writeSelect4} alt="Write Select 4" style={{ width: '50px', height: '50px' }} />
              <span style={{ color: '#19297C' }}> </span>
            </ProgressBarItem>
          </ProgressbarStyle>
          <FlowwriteContent>
            <div>
            <FlowInfoContainer>
              <FlowInfoBox>
                <ContentSelect>
                    <div style={{ marginLeft: "38px"}}> </div>
                </ContentSelect>
                <ContentSelectDetail>
                     
                </ContentSelectDetail>
                <RecommendRecreation content={recreationData} handleAddRecommendFlow={handleAddRecommendFlow}/>
                <ContentSelectDetail>
                     
                </ContentSelectDetail>
                <ScrapRecreation content={recreationData} handleAddScrapFlow={handleAddScrapFlow}/> {/*    */}
              </FlowInfoBox>
            </FlowInfoContainer>

            <FlowInfoContainer>
              <FlowInfoBox>
                <ContentInfo>
                  <div></div>
                  <div></div>
                </ContentInfo>
                <ContentInfoDetail>
                  <div style={{ marginLeft: "20px", marginTop: "56px" }}>
                    <div style={{ display: "flex", marginBottom: "8px" }}>
                      <div style={{ marginRight: "8px", fontSize: "16px", fontStyle: "normal", fontWeight: "600" }}></div>
                      <div style={{ listStyleType: "none" }}>
                        {selectedKeywords.map((keyword, index) => (
                        <li key={index}>{keyword}</li>
                        ))}
                      </div>
                    </div>
                    <div style={{ display: "flex" }}>
                      <div style={{ marginRight: "8px", fontSize: "16px", fontStyle: "normal", fontWeight: "600" }}>
                         
                      </div>
                      <div>{ playTime }</div>
                    </div>
                  </div>

                  <Line></Line>

                  <div style={{ marginTop: "29px" }}>
                    <div style={{ display: "flex", marginBottom: "8px" }}>
                      <div style={{ marginRight: "8px", fontSize: "16px", fontStyle: "normal", fontWeight: "600" }}>
                        
                      </div>
                      <div style={{ marginRight: "8px" }}> 1</div>
                      <div style={{ marginRight: "8px" }}> 2</div>
                      <div> 3</div>
                    </div>
                    <div style={{ display: "flex", marginBottom: "8px" }}>
                      <div style={{ marginRight: "8px", fontSize: "16px", fontStyle: "normal", fontWeight: "600" }}></div>
                      <div>, </div>
                    </div>
                    <div style={{ display: "flex", marginBottom: "8px" }}>
                      <div style={{ marginRight: "8px", fontSize: "16px", fontStyle: "normal", fontWeight: "600" }}>
                        
                      </div>
                      <div>30, 40</div>
                    </div>
                    <div style={{ display: "flex", marginBottom: "8px" }}>
                      <div style={{ marginRight: "8px", fontSize: "16px", fontStyle: "normal", fontWeight: "600" }}></div>
                      <div>40</div>
                    </div>
                  </div>
                </ContentInfoDetail>

                <ContentTitle>
                  <div style={{ marginLeft: "38px" }}> </div>
                </ContentTitle>

                {/* ContentTitleInput    */}
                <ContentTitleInput
                  type="text"
                  placeholder="  ."
                  value={flowTitle}
                  onChange={handleFlowTitleChange}
                />

                <FlowContainer>
                  <div style={{ width: "393px", textAlign: "center" }}>
                    {/* FlowTitle     */}
                    <FlowTitle hasTitle={!!flowTitle}>{flowTitle || " "}</FlowTitle>
                  </div>

                  {/*    */}
                  {infoBoxes.map((num) => (
                    <WriteRecreationInfo key={num} num={num} onDelete={handleDelete} />
                  ))}

                  {/*     WriteRecreationInfo */}
                  {[...Array(numOfRecreationInfo - 1)].map((_, index) => (
                    <WriteRecreationInfo key={index + 2} time={time} num={index + 2} onDelete={handleDelete} />
                ))}

                  <AddFlowButton onClick={handleAddFlow}>+</AddFlowButton>
                </FlowContainer>
              </FlowInfoBox>
            </FlowInfoContainer>

              <LastButton onClick={handleBeforeClick}>
                
              </LastButton>
              <SaveButton onClick={handleSaveClick}>
                
              </SaveButton>

            </div>
          </FlowwriteContent>
        </FlowWriteWrap>
      );
}

const FlowWriteWrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-color: #F7F8F9;
`;

const ProgressbarStyle = styled.div`
  width: 1356px;
  height: 156px;
  background-color: #FFF;
  border: 0.5px solid #CACDD2;
  border-radius: 20px;
  display: flex;
  margin-bottom: 33px;
  justify-content: center;
  align-items: center;
  margin-top: 38px;
`;

const ProgressBarItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  img {
    margin-right: 10px;
    margin-left: 10px;
  }

  span {
    color: #CACDD2;
    font-size: 24px;
    font-weight: 700;
  }
`;

const FlowwriteContent = styled.div`
  width: 1356px;
  background-color: #FFF;
  border: 0.5px solid #CACDD2;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  margin-bottom: 45px;
  padding-bottom: 57px;
`;

const ContentBase = styled.div`
  height: 83px;
  border-radius: 20px;
  border: 1px solid #cacdd2;
  background: white;
  display: flex;
  align-items: center;
  margin-bottom: 13px;
  gap: 225px;
  font-size: 24px;
  font-weight: 700;
`;

const ContentSelect = styled(ContentBase)`
  width: 606px;
  margin-left: 40px;
`;

const ContentInfo = styled(ContentBase)`
  width: 608px;
  flex-grow: 1;
  margin-left: 20px;
  justify-content: center;
`;

const ContentTitle = styled(ContentBase)`
  width: 608px;
  margin-left: 20px;
  margin-top: 30px;
`;

const FlowInfoContainer = styled.div`
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  margin-left: 28px;
  width: 608px;
`;

const FlowInfoBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ContentSelectDetail = styled.div`
  width: 608px;
  height: 51px;
  border-radius: 20px;
  background: #F7F8F9;
  margin-left: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
`;

const ContentInfoDetail = styled.div`
  width: 608px;
  height: 158px;
  border-radius: 20px;
  background: #F7F8F9;
  margin-left: 20px;
  display: flex;
`;

const ContentTitleInput = styled.input`
  width: 590px;
  height: 51px;
  border-radius: 20px;
  border: none;
  outline: none;
  background: #F7F8F9;
  margin-left: 20px;
  margin-bottom: 120px;
  padding-left: 18px;
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: 400;

  &::placeholder {
    color: #9FA4A9;
  }

  &:focus::placeholder {
    color: transparent;
  }
`;

const FlowContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 24px 70px;
  margin-bottom: 40px;
  align-items: flex-start;
  border-radius: 20px;
  border: 0.5px solid #9fa4a9;
  background: white;
`;

const Line = styled.div`
  border: 0.25px solid #cacdd2;
  height: 100px;
  margin-top: 29px;
  margin-left: 169px;
  margin-right: 20px;
`;

const FlowTitle = styled.div`
  margin-bottom: 59px;
  font-size: 24px;
  font-weight: 700;
  color: ${props => props.hasTitle ? 'black' : '#9FA4A9'};
`;

const AddFlowButton = styled.button`
  width: 246px;
  height: 53px;
  box-shadow: 0px 0px 20px 0px rgba(27, 29, 31, 0.15);
  border: none;
  border-radius: 20px;
  margin-top: 59px;
  margin-left: 75px;
  margin-bottom: 29px;
  font-size: 30px;
  background-color: #F7F8F9;

  &:hover {
    background-color: #5950ED;
    color: #fff;
    box-shadow: 0px 0px 20px 0px rgba(27, 29, 31, 0.15);
  }
`;

const LastButton = styled.button`
  width: 138px;
  height: 54px;
  color: #464C52;
  background-color: #fff;
  border: 1px solid #464C52;
  border-radius: 50px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
  margin-left: 510px;
  margin-top: 87px;

  /*  hover   */
  &:hover {
    background-color: #F7F8F9; /* hover    */
  }
`;

const SaveButton = styled.button`
  width: 138px;
  height: 54px;
  background-color: #4036ED;
  border: none;
  border-radius: 50px;
  font-size: 19px;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  margin-left: 60px;

  /*  hover   */
  &:hover {
    background-color: #3530ED; /* hover    */
  }
`;