import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import PenguinImg from "../assets/watchflow/penguin.png";
import Flow from "../components/flow/FlowBox.jsx";
import { useNavigate } from "react-router-dom";
import Pagination from "../components/pagination/Pagination.jsx";

const JWT_TOKEN =
  "eyJhbGciOiJIUzI1NiJ9.eyJpZCI6MiwiaWF0IjoxNzA3Mjk1MzkzLCJleHAiOjE5MDcyOTg5OTN9.yEvU_V98IMhnC09lEL_BdxU7aQTx69BclrAd9zjZL64";

export default function WatchFlow() {
  const navigate = useNavigate();
  const moveToMy = () => {
    navigate(`/flow/my`);
  };
  const moveToScrap = () => {
    navigate(`/flow/scrap`);
  };
  const moveToMakeFlow = () => {
    navigate(`/flow/write`);
  };

  //  
  const [datas, setDatas] = useState([]);
  //    
  const [loading, setLoading] = useState(false);
  //   
  const [currentPage, setCurrentPage] = useState(0);
  //     
  const datasPerPage = 6;
  //    
  const [scrap, setScrap] = useState(false);

  //     
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      const response = await axios.get(`https://dev.avab.shop/api/flows?page=${currentPage}`, {
        headers: {
          Authorization: `Bearer ${JWT_TOKEN}`,
        },
      });
      setDatas(response.data.result.flowList);
      setLoading(false);
    };
    fetchData();
    setScrap(false);
  }, [currentPage, scrap]);

  useEffect(() => {
    console.log(datas);
  }, [datas]);

  return (
    <MyFlowWrap>
      {/*    */}
      <MyFlowMenuContainer>
        <MyFlowMenuTitle></MyFlowMenuTitle>
        <MyFlowMenuBox style={{ backgroundColor: "#B1BEFF", fontWeight: "bold" }}> </MyFlowMenuBox>
        <MyFlowMenuBox onClick={moveToMy}>  </MyFlowMenuBox>
        <MyFlowMenuBox onClick={moveToScrap}>  </MyFlowMenuBox>
      </MyFlowMenuContainer>

      {/*   */}
      <MyFlowContainer>
        <div>
          <MyFlowBoxContainer>
            <MyFlowBoxImage src={PenguinImg} />
            <TitleBox>
              <MyFlowBoxTitle onClick={moveToMakeFlow}> </MyFlowBoxTitle>
            </TitleBox>
          </MyFlowBoxContainer>

          {/*     - Component */}
          <WatchFlowBoxParent>{datas && <Flow datas={datas} setScrap={setScrap} />}</WatchFlowBoxParent>
        </div>

        {/*  */}
        <Pagination
          currentPage={currentPage}
          totalDatas={datas.length}
          datasPerPage={datasPerPage}
          setCurrentPage={setCurrentPage}
        />
      </MyFlowContainer>
      <RightSide />
    </MyFlowWrap>
  );
}

const MyFlowWrap = styled.div`
  display: flex;
  background-color: #f7f8f9;
`;

const RightSide = styled.div`
  width: 5.7325%;
  background-color: #f7f8f9;
`;

// -  > ~ MyFlowMenuBox
const MyFlowMenuContainer = styled.div`
  box-sizing: border-box;
  background-color: white;
  border: 0.5px solid #cacdd2;
  border-bottom: 0;
  width: 320px;
  font-size: 24px;
`;

const MyFlowMenuTitle = styled.div`
  padding: 2.5vh 0vh 2.5vh 3vh;
`;

const MyFlowMenuBox = styled.div`
  border: 0.5px solid #cacdd2;
  text-align: center;
  justify-content: center;
  padding: 1.5vh 3vh;
  cursor: pointer;
`;

const MyFlowContainer = styled.div`
  background-color: white;
  padding-top: 30px;
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  border-right: 0.5px solid #cacdd2;
`;

//   
const MyFlowBoxContainer = styled.div`
  display: flex;
  align-items: center;
`;

const MyFlowBoxImage = styled.img`
  width: 199.65px;
  height: 199.65px;
  z-index: 2;
`;

const TitleBox = styled.div`
  background-color: #19297c;
  border-radius: 15vh;
  width: 527px;
  height: 109px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  position: relative;
  left: -23.65px;
  cursor: pointer;

  &:hover {
    background-color: #4036ed;
    transition: 0.2s;
  }
`;

//   - Grid
const WatchFlowBoxParent = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 370px);
  row-gap: 20px;
  column-gap: 120px;
  margin-top: 39px;
`;

const MyFlowBoxTitle = styled.div`
  color: white;
  width: 343px;
  height: 57px;
  font-weight: bold;
  font-size: 47px;
`;
